/*Creator sections*/
.creator-section{
    height: 100%;
}

/*CVTimeAndPlaceEntry.js*/
.creator-entry{
    display: block;
    text-align: center;
    overflow: hidden;
    margin: auto;
}
.creator-entry-header{
    font-size: 1.4em;
    font-weight: bold;
    text-decoration: underline;
    text-align: left;
}
.warning div input {
    background-color: rgb(212, 52, 52);
}
.warning div input:hover {
    background-color: red
}
.hide{
    display: none;
}
.info-item label{
    margin: 10px 0px
}
.info-item .textarea-box textarea{
    margin: 20px 0;
    width: 100%;
}
.info-item .textarea-box{
    width: 100%;
}