/*Button.js*/
.btn-custom{
    margin: auto;
}
.btn-custom-input
{
    display: block;
    margin: auto;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-color:lightblue;
    outline: none;
    border: 2px inset rgb(6, 202, 236);
    padding: 10px;
    font-family: inherit;
    font-size: 0.9em;
    border-radius: 10px;
    font-weight: bolder;
}
.btn-custom-input:hover{
    background-color: aqua;
}
/*Instructions.js*/
.creator-instructions{
    display: block;
}
.creator-instructions-p{
    display: block;
}
/* DateSelection.js*/
.dateselector{
    display: block;
    overflow: auto;
    width: fit-content;
    margin : 10px auto;
    text-align: center;
}
.dateselector-header{
    display: block;
    font-weight: bold;
    font-size: 1.2em;
    padding: 20px 0;
    width: 100%;
    text-decoration: underline solid red 2px;
    text-align: center;
}
.dateselector-items{
    margin: 10px auto;
    height: fit-content;
    overflow: hidden;
    min-width: 300px;
    width: fit-content;
}
.dateselector-item{
    display: inline-block;
    margin: auto;
    min-width: 80px;
}
.dateselector-item-label{
    padding: 10px 0;
    font-weight: bolder;
    font-size: 1.1em;

}
.dateselector-item-input select{
    font-size: inherit;
    width: 75px;
}
/* TextInput.js */

.text-input-box{
    font-size: 1em;
    width: 100%;
    max-width: 500px;
    margin: auto;
}

.text-input-label{
    width: 100%;
    cursor: pointer;
    
}
.text-input-label-main{
    font-weight: bold;
    font-size: 1.2em;
    padding: 20px 0;
    width: 100%;
    text-decoration: underline solid red 2px;
    text-align: center;
}
.text-input-element{
    padding: 10px;
    text-align: center;
}
.text-input{
    padding: 10px;
    border-radius: 5px;
    font-size: inherit;
    font-style: inherit;
    width: 100%; 
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;
    text-align: center;
}

.text-input:active, .text-input:focus{
    border-color: blue;
}
/* BottomNavBar.js */
.bottom-nav{
    display: block;
    width: 80%;
    min-width: 400px;
    margin: auto;
    padding: 20px;
    text-align: center;
}
.bottom-nav-element{
    width: 50%;
    display: inline-block;
}
@media only screen and (max-width:600px) {

    .bottom-nav{
        display: block;
        width: 80%;
        padding: 20px;
        min-width: 0px;
    }

    .bottom-nav-element{
        width: 100%;
        display: block;
        margin: 10px auto;
    } 
}
/* Line.js */
.line{
    border-bottom: 1px outset green;
    margin: 20px 0;
}
/* CheckInput.js */
.entry-inprogress{
    margin: 20px 0;
    text-align: center;
}
.entry-inprogress .checkbox-label{
    margin-left: 10px;
    user-select: none;
}
/* ImageUpload.js */
.image-upload{

}
#image-box{
    text-align: center;
    padding: 10px;
}
/* TextInputWithInstructions.js */
.text-input-extended{
    text-align: center;
}
.text-input-instructions{
    padding: 10px;
    text-align: center;
}
/* TextArea.js*/
.textarea-box{
    margin: 10px auto;
    text-align: center;
    width: 80%;
}
.textarea-box label{

}
.textarea-box textarea{
    margin: 10px 0;
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
    height: 200px;
    text-align: center;
    font: inherit;
}







.error{
    color: red;
    font-weight:  bold;
    font-style: italic;
    text-align: center;
}


.error-border{
    border-color: red;
}

.creator-section{
    width: fit-content;
    margin: auto;
}

/* Prompt.js */

.prompt{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.425);
}
.prompt-box{
    position: absolute;
    background-color: white;
    width: 100%;
    max-width: 500px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.prompt-header{
    text-align: center;
    padding: 20px;
    background-color: lightblue;
}
.prompt-choice{
    margin: 20px;
    text-align: center;
}
.prompt-text{
    margin: 20px;
    text-align: center;
}
.prompt-text p {
    margin: 30px;
}
.prompt-button{
    display: inline-block;
    width: 40%;
}
/*ChooseFormat.js*/
.choice img{
    width: 300px;
}
.choice input{
    width: 100%;
    margin: 20px 0px
}
.choice{
    text-align: center;
    padding: 5px;
    margin: 5px;
    border: 1px solid black;
}
.choose-design-header{
    text-align: center;
    margin: 20px;
    font-size: 1.2em;
    font-weight: normal;
}
.chosen{
    border-color: blue;
}

/* RadioGroup.js */
.radio-group{
    text-align: center;
}
.radio-item{
    margin: 10px
}
.radio-item input{
    margin-right: 10px;
}