#creator-settings{
    display: flex;
    margin: 1em;
    height: fit-content;
}
.creator-header-logo{
    height: 50px;
    text-align: center;
    padding: 10px;
}
.creator-header-logo img{
    height : 100%
}

#creator-lang{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: end;
}
#creator-lang select{
    height: 2em;
}
#creator-font-size{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:0.2em

}
#lang-text{
    padding-right: 0.4em;
}
.border{
    padding: 2px;
    user-select: none;
    cursor: pointer;
    background-color: yellow;
}